.search-div {
  width: 100%;
  background-color: #4e31aa;
  justify-content: center;
  padding-top: 10px;
  padding-bottom: 10px;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

button {
  background-color: #c0eef2;
}

button:hover, button:focus {
  cursor: pointer;
  background-color: #e9f8f9;
}

.search-form input, button {
  border: none;
  border-radius: 4px;
  padding: 4px 8px;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 10px;
  margin-top: 70px;
  margin-bottom: 20px;
  display: flex;
}

.photo-card {
  min-width: 300px;
  background-color: #f0f8ff;
  border-radius: 5px;
  flex-basis: 10%;
  box-shadow: 0 .5px 1.3px #00f;
}

.photo-card img {
  width: 100%;
  height: 120px;
  object-fit: cover;
  border-radius: 5px 5px 0 0;
}

.info {
  justify-content: space-between;
  gap: 8px;
  padding: 4px 10px;
  display: flex;
}

.info-item {
  flex-direction: column;
  align-items: center;
  line-height: .7;
  display: flex;
}

.load-more {
  border-radius: 4px;
  margin-left: auto;
  margin-right: auto;
  padding: 4px 8px;
  display: block;
}

.hidden {
  display: none;
}

